import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Layout from '../layout/index'

export const routes = [
    {
        path: '/login',
        component: () => import('@/views/login'),
        name: '用户登录',
        meta: { title: '用户登录', navShow: false, backShow: false }
    },
    {
        path: '/404',
        component: () => import('@/views/system-manage/404'),
        meta: { title: '404', navShow: false, backShow: false }
    },
    {
        path: '/401',
        component: () => import('@/views/system-manage/401'),
        meta: { title: '401', navShow: false, backShow: false }
    },
    {
        path: '/',
        component: Layout,
        redirect: '/dashboard',
        children: [
            {
                path: '/dashboard',
                component: () => import('@/views/home'),
                name: '首页',
                meta: { title: '首页', navShow: false, backShow: false }
            },
            {
                path: '/park/list/:id',
                component: () => import('@/views/park-manage/park-list'),
                name: '园区管理',
                meta: { title: '园区管理', navShow: false, backShow: false }
            },
            {
                path: '/plot/list/:id',
                component: () => import('@/views/plot-manage/plot-list'),
                name: '地块管理',
                meta: { title: '地块管理', navShow: false, backShow: true }
            }
        ]
    },

    {
        path: '/monitor',
        component: Layout,
        children: [
            {
                path: '/monitor/index',
                component: () => import('@/views/template-monitor/monitor-index'),
                name: '环境监控',
                meta: { title: '环境监控', navShow: true, backShow: true }
            },
            {
                path: '/monitor/weather',
                component: () => import('@/views/template-monitor/view-weather'),
                name: '气象环境监测',
                meta: { title: '气象环境监测', navShow: true, backShow: true }
            },
            {
                path: '/monitor/soil',
                component: () => import('@/views/template-monitor/view-soil'),
                name: '土壤墒情监测',
                meta: { title: '土壤墒情监测', navShow: true, backShow: true }
            },
            {
                path: '/monitor/growth',
                component: () => import('@/views/template-monitor/view-growth'),
                name: '作物长势监测',
                meta: { title: '作物长势监测', navShow: true, backShow: true }
            },
            {
                path: '/monitor/pests',
                component: () => import('@/views/template-monitor/view-pests'),
                name: '病虫害监测',
                meta: { title: '病虫害监测', navShow: true, backShow: true }
            },
        ]
    },
    {
        path: '/farming',
        component: Layout,
        children: [
            {
                path: '/farming/task',
                component: () => import('@/views/template-farming/view-task'),
                name: '任务管理',
                meta: { title: '任务管理', navShow: true, backShow: true }
            },
            {
                path: '/farming/seed',
                component: () => import('@/views/template-farming/view-seed'),
                name: '种子管理',
                meta: { title: '种子管理', navShow: true, backShow: true }
            },
            {
                path: '/farming/tour',
                component: () => import('@/views/template-farming/view-tour'),
                name: '巡园管理',
                meta: { title: '巡园管理', navShow: true, backShow: true }
            },
            {
                path: '/farming/work',
                component: () => import('@/views/template-farming/view-work'),
                name: '工作汇报',
                meta: { title: '工作汇报', navShow: true, backShow: true }
            },
        ]
    },
    {
        path: '/trace',
        component: Layout,
        children: [
            {
                path: '/trace/batch',
                component: () => import('@/views/template-trace/view-batch'),
                name: '溯源管理',
                meta: { title: '溯源管理', navShow: true, backShow: true }
            },
            {
                path: '/trace/certify',
                component: () => import('@/views/template-trace/view-certify'),
                name: '认证管理',
                meta: { title: '认证管理', navShow: true, backShow: true }
            },
            {
                path: '/trace/detect',
                component: () => import('@/views/template-trace/view-detect'),
                name: '检测管理',
                meta: { title: '检测管理', navShow: true, backShow: true }
            },
            {
                path: '/trace/process',
                component: () => import('@/views/template-trace/view-process'),
                name: '加工管理',
                meta: { title: '加工管理', navShow: true, backShow: true }
            },
            {
                path: '/trace/delivery',
                component: () => import('@/views/template-trace/view-delivery'),
                name: '配送管理',
                meta: { title: '配送管理', navShow: true, backShow: true }
            }
        ]
    }
]

export default new Router({
    //mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes: routes
})



