/**
 * @description 系统全局配置
 */
export default {
    /**
     * @description 记住密码状态下的token在Cookie中存储的天数，默认1天
     */
    tokenCookieExpires: 1,

    /**
     * @description 记住密码状态下的密码在Cookie中存储的天数，默认1天
     */
    passCookieExpires: 1,

    /**
     * @description 此处修改网站名称
     */
    webName: '农产品溯源管理系统',

    /**
     * @description token key
     */
    TokenKey: 'SUYUAN-TOEKN',
  
    /**
     * @description 请求超时时间，毫秒（默认2分钟）
     */
    timeout: 1200000,
  
    /**
     * @description 是否显示设置的底部信息
     */
    showFooter: false,
  
    /**
     * @description 底部文字，支持html语法
     */
    footerTxt: '版权所有 2019 国财财务订单财务管理系统',
  }
  


