<template>
    <div>
        <el-row type="flex" justify="space-between" align="middle">
            <el-col :span="12">
                <div class="top-logo flex flex-align-center">
                    <img :src="logoSrc" />
                    <h1>{{ $store.state.settings.webName }}</h1>
                </div>
            </el-col>
            <el-col :span="12">
                <el-row type="flex" justify="end" align="middle">
                    <el-col :span="20">
                        <div class="text-right">
                            <el-input style="width:200px" size="mini" placeholder="请输入内容" suffix-icon="el-icon-search" v-model="searchKey"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="4">
                        <div class="top-user text-right">
                            <el-dropdown @command="handleCommand">
                                <span class="el-dropdown-link flex flex-align-center">
                                    <el-avatar icon="el-icon-user-solid"></el-avatar>
                                    <i class="el-icon-arrow-down el-icon--right"></i>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item command="commandAccount">基本信息</el-dropdown-item>
                                    <el-dropdown-item command="commandPassword">修改密码</el-dropdown-item>
                                    <el-dropdown-item command="commandMessage">系统通知</el-dropdown-item>
                                    <el-dropdown-item divided>退出登录</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        <div class="flex flex-align-center flex-pack-justify">
            <div class="action-back">
                <el-button type="text" v-if="backShow" @click="back()">
                    <i class="el-icon-arrow-left"></i>
                    <span>返回</span>
                </el-button>
            </div>
            <div class="action-menu">
                <div class="flex flex-align-center" v-if="navShow">
                    <router-link to="/monitor/index"><div :class="['menu-item', {'on': navIndex == 1 }]">环境监控</div></router-link>
                    <router-link to="/farming/task"><div :class="['menu-item', {'on': navIndex == 2 }]">农事管理</div></router-link>
                    <router-link to="/trace/batch"><div :class="['menu-item', {'on': navIndex == 3 }]">溯源管理</div></router-link>
                </div>
            </div>
            <div class="action-org">
                <el-button type="text" @click="dialogWarningVisible = true">
                    <i class="el-icon-bell"></i>
                    <span>预警中心</span>
                </el-button>
                <el-button type="text" @click="drawerVisible = true">
                    <i class="el-icon-office-building"></i>
                    <span>组织列表</span>
                </el-button>
                <el-drawer title="组织列表" :visible.sync="drawerVisible">
                    <div class="org-list">
                        <el-input size="small" placeholder="输入关键字进行过滤" v-model="drawerKey"></el-input>
                        <el-tree 
                            ref="tree"
                            default-expand-all 
                            :data="drawerList" 
                            :props="defaultProps" 
                            :filter-node-method="filterNode">
                        </el-tree>
                    </div>
                </el-drawer>
            </div>
        </div>
        <el-dialog title="基本信息" width="600px" size="small" :visible.sync="dialogAccountVisible" :append-to-body="true">
            <el-form ref="form1" size="small" :rules="rules1" :model="form1" label-width="150px">
                <el-form-item label="用户名" prop="userName">
                    <el-input style="width:260px;" v-model="form1.userName"></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="trueName">
                    <el-input style="width:260px;" v-model="form1.trueName"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input style="width:260px;" v-model="form1.email"></el-input>
                </el-form-item>
                <el-form-item label="电话" prop="telphone">
                    <el-input style="width:260px;" v-model="form1.telphone"></el-input>
                </el-form-item>
                <el-form-item label="备注" prop="remark">
                    <el-input style="width:260px;" v-model="form1.remark"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-radio-group v-model="form1.sex">
                        <el-radio label="男"></el-radio>
                        <el-radio label="女"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="头像" prop="avatar">
                    <el-input style="width:260px;" v-model="form1.avatar"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="small" @click="dialogAccountVisible = false">取消</el-button>
                <el-button size="small" type="primary" @click="saveAccount">保存</el-button>
            </span>
        </el-dialog>
        
        <el-dialog title="修改密码" width="600px" size="small" :visible.sync="dialogPasswordVisible" :append-to-body="true">
            <el-form ref="form2" size="small" :rules="rules2" :model="form2" label-width="150px">
                <el-form-item label="原密码" prop="oldPassword">
                    <el-input style="width:260px;" type="password" v-model="form2.oldPassword"></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="newPassword">
                    <el-input style="width:260px;" type="password" v-model="form2.newPassword"></el-input>
                </el-form-item>
                <el-form-item label="新密码确认" prop="newPasswords">
                    <el-input style="width:260px;" type="password" v-model="form2.newPasswords"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="small" @click="dialogPasswordVisible = false">取消</el-button>
                <el-button size="small" type="primary" @click="savePassword">保存</el-button>
            </span>
        </el-dialog>

        <el-dialog title="系统通知" width="90%" size="small" :visible.sync="dialogMessageVisible" :append-to-body="true">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-date-picker
                        type="datetimerange"
                        size="small"
                        v-model="form3.searchDate" 
                        :picker-options="pickerMessageOptions"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        class="m-r-15">
                    </el-date-picker>
                    <el-button size="small" type="primary" @click="handleReaded">全部标记为已读</el-button>
                </div>
                <div class="flex flex-align-center">
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="请输入关键词" suffix-icon="el-icon-search" v-model="form3.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="searchMessage">搜索</el-button>
                </div>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="messageList" size="small" border stripe style="width: 100%">
                        <el-table-column prop="state" label="状态"></el-table-column>
                        <el-table-column prop="content" label="内容" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="time" label="时间"></el-table-column>
                        <el-table-column prop="source" label="来源"></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="form3.currentPage"
                        :page-size="form3.pageSize"
                        :total="form3.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
        </el-dialog>

        <el-dialog title="预警中心" width="90%" size="small" :visible.sync="dialogWarningVisible" :append-to-body="true">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-select class="m-r-15" size="small" v-model="form4.searchType" placeholder="请选择">
                        <el-option
                            v-for="item in warnTypeList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                    <el-date-picker
                        type="datetimerange"
                        size="small"
                        v-model="form4.searchDate" 
                        :picker-options="pickerWarningOptions"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        class="m-r-15">
                    </el-date-picker>
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="请输入关键词" suffix-icon="el-icon-search" v-model="form4.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="searchWarning">搜索</el-button>
                </div>
                <el-button size="small" type="primary" @click="dialogWarnRuleVisible = true">预警规则</el-button>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="warningList" size="small" border stripe style="width: 100%">
                        <el-table-column prop="type" label="类型"></el-table-column>
                        <el-table-column prop="time" label="时间"></el-table-column>
                        <el-table-column prop="content" label="内容" show-overflow-tooltip></el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        :current-page.sync="form4.currentPage"
                        :page-size="form4.pageSize"
                        :total="form4.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
        </el-dialog>

        <el-dialog title="预警规则" width="90%" size="small" :visible.sync="dialogWarnRuleVisible" :append-to-body="true">
            
        </el-dialog>
    </div>
</template>

<script>
import Config from '@/config'
import logo from '@/assets/logo.png'
export default {
    data(){
        return {
            logoSrc: logo,
            searchKey: '',

            backShow: false,
            navShow: false,
            navIndex: 1,

            // 组织列表
            drawerVisible: false,
            drawerKey: '',
            drawerList: [{
                id: 1,
                label: '广西田诚慧绿色农业有限公司',
                children: [{
                    id: 2,
                    label: '园区1',
                    children: [{
                        id: 5,
                        label: '园区1-地块1'
                    }, {
                        id: 6,
                        label: '园区1-地块2'
                    }, {
                        id: 7,
                        label: '园区1-地块3'
                    }]
                },{
                    id: 3,
                    label: '园区2',
                    children: [{
                        id: 8,
                        label: '园区2-地块1'
                    }, {
                        id: 9,
                        label: '园区2-地块2'
                    }]
                },{
                    id: 4,
                    label: '园区3',
                    children: [{
                        id: 10,
                        label: '园区3-地块1'
                    }, {
                        id: 11,
                        label: '园区3-地块2'
                    }]
                }]
            }],
            defaultProps: {
                children: 'children',
                label: 'label'
            },

            // 账户设置
            dialogAccountVisible: false,
            form1:{
                userName: '',
                trueName: '',
                email: '',
                telphone: '',
                remark: '',
                sex: '男',
                avatar: ''
            },
            rules1: {
                userName: [
                    { required: true, message: '请填写用户名', trigger: 'blur' },
                ],
                trueName: [
                    { required: true, message: '请填写姓名', trigger: 'blur' }
                ]
            },

            // 修改密码
            dialogPasswordVisible: false,
            form2:{
                oldPassword: '',
                newPassword: '',
                newPasswords: ''
            },
            rules2: {
                oldPassword: [
                    { required: true, message: '请填写原密码', trigger: 'blur' },
                ],
                newPassword: [
                    { required: true, message: '请填写新密码', trigger: 'blur' }
                ],
                newPasswords: [
                    { required: true, message: '请填写新密码确认', trigger: 'blur' }
                ]
            },

            // 系统消息
            dialogMessageVisible: false,
            pickerMessageOptions: {
                shortcuts: [{
                    text: '最近一周',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近一个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近三个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            },
            form3: {
                searchDate: '',
                searchKey: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            messageList: [
                {
                    state: 0,
                    content: '',
                    time: '',
                    source: ''
                }
            ],

            // 预警中心
            dialogWarningVisible: false,
            dialogWarnRuleVisible: false,
            pickerWarningOptions: {
                shortcuts: [{
                    text: '最近一周',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近一个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近三个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            },
            warnTypeList: [
                {
                    value: '0',
                    label: '全部',
                },
                {
                    value: '1',
                    label: '现场设备预警',
                },
                {
                    value: '2',
                    label: '天气预报预警',
                },
                {
                    value: '3',
                    label: '病虫害预警',
                },
                {
                    value: '4',
                    label: '农事任务预警',
                }
            ],
            form4: {
                searchType: '',
                searchDate: '',
                searchKey: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            warningList: [
                {
                    type: '高温预警',
                    time: '2019-10-26 16:47',
                    content: '注意预防低温冻害'
                },
                {
                    type: '高温预警',
                    time: '2019-10-26 16:47',
                    content: '注意预防低温冻害'
                },
                {
                    type: '高温预警',
                    time: '2019-10-26 16:47',
                    content: '注意预防低温冻害'
                }
            ],
        }
    },
    computed: {
        tableHeight() {
            return document.documentElement.clientHeight - 500;
        }
    },
    methods: {
        // 返回
        back(){
            this.$router.go(-1)
        },

        // 组织列表过滤
        filterNode(value, data) {
            if (!value) return true;
            return data.label.indexOf(value) !== -1;
        },

        // 切换
        handleCommand(command) {
            if(command == "commandAccount"){
                this.dialogAccountVisible = true
            }else if(command == "commandPassword"){
                this.dialogPasswordVisible = true
            }else if(command == "commandMessage"){
                this.dialogMessageVisible = true
            }
        },

        // 修改基本资料
        saveAccount(){

        },

        // 修改密码
        savePassword(){

        },

        // 标记为已读
        handleReaded(){

        },

        // 系统消息
        searchMessage(){
            console.log(this.form3.searchKey)
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        },

        // 预警中心
        searchWarning(){

        },

        // 初始化路由
        initRoute(){
            // 是否展示顶部菜单
            if(this.$route.meta.navShow){
                this.navShow = true
            }else{
                this.navShow = false
            }
            // 是否展示返回按钮
            if(this.$route.meta.backShow){
                this.backShow = true
            }else{
                this.backShow = false
            }
            // 当前顶部菜单
            if(this.$route.path.indexOf("monitor") > 0){
                this.navIndex = 1
            }else if(this.$route.path.indexOf("farming") > 0){
                this.navIndex = 2
            }else if(this.$route.path.indexOf("trace") > 0){
                this.navIndex = 3
            }
        }
    },
    created(){
        this.initRoute()
    },
    watch: {
        drawerKey(val) {
            this.$refs.tree.filter(val);
        },
        $route(to, from){
            // 是否展示顶部菜单
            if(to.meta.navShow){
                this.navShow = true
            }else{
                this.navShow = false
            }
            // 是否展示返回按钮
            if(to.meta.backShow){
                this.backShow = true
            }else{
                this.backShow = false
            }
            // 当前顶部菜单
            if(to.path.indexOf("monitor") > 0){
                this.navIndex = 1
            }else if(to.path.indexOf("farming") > 0){
                this.navIndex = 2
            }else if(to.path.indexOf("trace") > 0){
                this.navIndex = 3
            }
        }
    },
}
</script>

<style>

</style>


