import { login, getInfo } from '@/api/login'
import { getToken, setToken, removeToken } from '@/utils/auth'
import { Notification } from 'element-ui'

const user = {
    state: {
        token: getToken(),
        user: {},
        roles: [],
        company: 0
    },

    mutations: {
        SET_TOKEN: (state, token) => {
            state.token = token
        },
        SET_USER: (state, user) => {
            state.user = user
        },
        SET_ROLES: (state, roles) => {
            state.roles = roles
        },
        SET_COMPANY: (state, company) => {
            state.company = company
        }
    },

    actions: {
        // 登录
        Login({ commit }, loginForm) {
            return new Promise((resolve, reject) => {
                login(loginForm).then(res => {
                    if(res.code == 1){
                        Notification.success({
                            title: res.msg
                        })
                        //setToken(res.token)
                        setToken('suyuanToken')
                        commit('SET_TOKEN', 'ceshiToken')
                        //commit('SET_TOKEN', res.token)
                        //setUserInfo(res, commit)
                        resolve()
                    }else{
                        Notification.error({
                            title: res.msg
                        })
                    }
                }).catch(error => {
                    reject(error)
                })
            })
        },

        // 获取用户信息
        GetInfo({ commit }) {
            return new Promise((resolve, reject) => {
                let res = {
                    code: 1,
                    data: {companyid: 1, companyname: "扬州瑞丰信息科技有限公司"},
                    companyid: 1,
                    companyname: "扬州瑞丰信息科技有限公司",
                    msg: "登录成功!"
                }
                setUserInfo(res, commit)
                resolve(res)
                /* getInfo().then(res => {
                    setUserInfo(res, commit)
                    resolve(res)
                }).catch(error => {
                    reject(error)
                }) */
            })
        },

        // 登出
        LogOut({ commit }) {
            return new Promise((resolve, reject) => {
                commit('SET_TOKEN', '')
                commit('SET_ROLES', [])
                commit('SET_COMPANY', 0)
                removeToken()
                resolve()
            })
        },
    }
}

export const setUserInfo = (res, commit) => {
    // 如果没有任何权限，则赋予一个默认的权限，避免请求死循环
    /* if (res.roles.length === 0) {
        commit('SET_ROLES', ['ROLE_SYSTEM_DEFAULT'])
    } else {
        commit('SET_ROLES', res.roles)
    }
    commit('SET_USER', res) */
    commit('SET_COMPANY', res.data.companyid)
}

export default user



