const baseUrl = process.env.BASE_API
const api = {
    state: {
        // 图片上传
        imagesUploadApi: baseUrl + '/api/pictures',
        // 修改头像
        updateAvatarApi: baseUrl + '/api/users/updateAvatar',
    }
}

export default api



